.academy__link {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: normal;
  text-align: left;
  color: #444444;
}

.academy {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (min-width: @tablet-width) {
    width: 162px;
  }
}

.academy__logo {
  height: 34px;
  margin-top: 5px;
}

.academy__logo-svg {
  width: 27px;
  height: 34px;
}
