.program-extra__item {
  margin-bottom: 28px;
  .flex-row;

  @media (min-width: @tablet-width) {
    width: 50%;
    margin-bottom: 24px;
  }

  @media (min-width: @desktop-width) {
    width: 25%;
  }
}

.program-extra__checkbox {
  width: 30px;
  height: 30px;

  @media (min-width: @tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.program-extra__label {
  line-height: 30px;
  margin-left: 16px;

  @media (min-width: @tablet-width) {
    line-height: 40px;
    margin-left: 20px;
  }
}
